html {
  font-family: sans-serif;
}

h1 {
  margin: 20px 0;
}

p {
  line-height: 1.5em;
  margin: 0 0 6px 0;
}

.quiz {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
  text-align: left;
  width: 600px;
  margin: auto;
}

button {
  background: #e8e8e8;
  border: 0;
  padding: 10px 20px;
  cursor: pointer;
  border-bottom: 3px solid #c9c9c9;
  border-radius: 3px;
}

button.next {
  background: #6ad85c;
  border-bottom: 3px solid #5abc4e;
}

button.start,
button.restart {
  margin-top: 20px;
}

.randomiser {
  text-align: center;
}
.question {
  width: 100%;
}

.questionText {
  font-size: 1.2em;
  margin: 20px 0;
}

.answers {
  margin-bottom: 20px;
}

.answer {
  padding: 4px;
  text-align: center;
  background: #f3f3f3;
  margin-bottom: 5px;
  border-radius: 3px;
  cursor: pointer;

  &.selected {
    background: #df3636;
  }
  &.correct,
  &.incorrect {
    background: #ffcc00;
    font-weight: bold;
  }
}

.answerStatus {
  font-weight: bold;
  margin-bottom: 20px;
}

.progressBar {
  width: 100%;
  background: #f3f3f3;
  height: 20px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}

.progressBar .text {
  font-size: 0.7em;
  position: absolute;
  z-index: 10;
}

.fade-in-image {
  animation: fadeIn 3s;
  -webkit-animation: fadeIn 3s;
  -moz-animation: fadeIn 3s;
  -o-animation: fadeIn 3s;
  -ms-animation: fadeIn 3s;
  border: solid 1px #00ff00;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;
  width: 80%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
